import React, { PureComponent } from 'react';
import { ThemeProvider } from 'styled-components';
import matches from 'lodash/fp/matches';
import head from 'lodash/fp/head';
import update from 'lodash/fp/update';
import map from 'lodash/fp/map';
import find from 'lodash/fp/find';
import WordPress from './WordPress';
import * as theme from './theme';
import FilterItem from './FilterItem';
import Nav from './Nav';
import NavItem from './NavItem';
import Show from './Show';
import { Progress, Indeterminate } from './Progress';
import { Container, Header, Logo, Filters, Shows } from './App.styles';

const wp = new WordPress('https://publishow.000webhostapp.com/wp-json/wp/v2/');

const SHOWS_FILTER = { tags: [''] };
const LECTURES_FILTER = { tags: [''] };
const TODAY = new Date();

const AREA_OPTIONS = [
  {
    label: '',
    value: null,
  },
  {
    value: 'tlv',
    label: ' ',
  },
  {
    value: 'north',
    label: '',
  },
  {
    value: 'south',
    label: '',
  },
];

const REQUIRED_REGISTRATION_OPTIONS = [
  {
    label: ' ',
    value: null,
  },
  {
    label: '',
    value: true,
  },
  {
    label: '',
    value: false,
  },
];

const PRICE_OPTIONS = [
  {
    label: ' ',
    value: null,
  },
  {
    label: '',
    value: 0,
  },
  {
    label: ' 20',
    value: 20,
  },
  {
    label: ' 50',
    value: 50,
  },
];

class App extends PureComponent {
  state = {
    selectedNavItem: 'all',
    filters: {
      area: null,
      price: null,
      date: null,
      requiresRegistration: null,
    },
    shows: [],
  };

  async componentDidMount() {
    this.setState({ loading: true });
    const tags = await wp.tags.get();
    const posts = await wp.posts.get({ _embed: true });
    this.setState({
      loading: false,
      shows: posts.map(
        update(
          'tags',
          map(id => {
            const tag = find({ id }, tags);
            return tag.name;
          }),
        ),
      ),
    });
  }

  selectNavItem = selectedNavItem => {
    this.setState({ selectedNavItem });
  };

  handleFilterChange = (value, name) => {
    this.setState(state => ({
      filters: {
        ...state.filters,
        [name]: value,
      },
    }));
  };

  render() {
    const { selectedNavItem, filters, shows, loading } = this.state;
    return (
      <ThemeProvider theme={theme}>
        <Container>
          <Header>
            <Logo />
            <Nav selected={selectedNavItem} onSelect={this.selectNavItem}>
              <NavItem title="" name="all" filter={null} />
              <NavItem title="" name="shows" filter={SHOWS_FILTER} />
              <NavItem title="" name="lectures" filter={LECTURES_FILTER} />
            </Nav>
            <Filters>
              <FilterItem
                type="select"
                label=""
                name="area"
                value={filters.area}
                onChange={this.handleFilterChange}
                options={AREA_OPTIONS}
              />
              <FilterItem
                type="select"
                label=""
                name="price"
                value={filters.price}
                onChange={this.handleFilterChange}
                options={PRICE_OPTIONS}
              />
              <FilterItem
                type="date"
                label=""
                name="date"
                value={filters.date}
                onChange={this.handleFilterChange}
                minDate={TODAY}
                maxDate={null}
              />
              <FilterItem
                type="select"
                label=" "
                name="requiresRegistration"
                value={filters.requiresRegistration}
                onChange={this.handleFilterChange}
                options={REQUIRED_REGISTRATION_OPTIONS}
              />
            </Filters>
          </Header>
          <Progress loading={loading}>
            <Indeterminate />
          </Progress>
          <Shows>
            {shows
              .filter(matches(selectedNavItem.filter))
              .map(show => (
                <Show
                  key={show.id}
                  image={head(show._embedded['wp:featuredmedia']).source_url}
                  title={show.title.rendered}
                  location={show.acf.location.address}
                  tags={show.tags}
                  time={Number(show.acf.time)}
                  entrance={Number(show.acf.entrance)}
                  registrationLink={show.acf.registrationLink}
                />
              ))}
          </Shows>
        </Container>
      </ThemeProvider>
    );
  }
}

export default App;



